import axios from 'axios'
import React, { Component } from 'react'
import Subnav from '../Subnav';
import Navbar from '../Navbar';
import { Link } from 'react-router-dom';
import ScrollToTop from "react-scroll-to-top";
import { CartProvider } from "react-use-cart";

 class Allproducts extends Component {
    state = {
        persons :[

        ]
    }
    componentDidMount(){
        axios.get("/assets/js/Api.json")
        .then((result)=>{
            this.setState(
                {
                    persons :result.data.products
            
                    
                }

            )
        })
    }
  render() {
    return (
      <CartProvider>
        <Subnav/>
        <Navbar/>
        <ScrollToTop smooth
        style={{backgroundColor:"#000"}}
        color='white'
        />
        <div className='section properties'>
            <div className='container'>
                <div className='row properties-box'>
               
                    {this.state.persons.map((items)=>(
                       <div class="col-lg-4 col-md-6 align-self-center mb-30 properties-items col-md-6 adv">
                       <div class="item">
                         <a href="/details">
                            <img src={items.images[0]} alt="" style={{height:"300px"}}/>
                            </a>
                         <span class="category">{items.title}</span>
                         <h6>{items.price}</h6>
                         <h4><Link to="/details"className='text-dark' >{items.description}</Link></h4>
                         <ul>
                           <li>rating: <span>{items.rating}</span></li>
                           <li>brand: <span>{items.brand}</span></li>
                           <li>category: <span>{items.category}</span></li>
                           <li>price: <span>{items.price}</span></li>
                           <li>stock: <span>{items.stock}</span></li>
                         </ul>
                         <div class="main-button">
                           <a href="property-details.html">Schedule a visit</a>
                         </div>
                       </div>
                     </div>

                    ))}
                </div>
            </div>

        </div>

      </CartProvider>
    )
  }
}

export default Allproducts