import * as React from "react";
import { createRoot } from "react-dom/client";
import App from "./App";
import {
  createBrowserRouter,
  RouterProvider,
  Route,
  Link,
} from "react-router-dom";
import Properties from "./Properties/Properties";
import Alldetail from "./Details/Alldetail";
import Contact from "./Contactus/Contact";
import Allproducts from "./Proudcts/Allproducts";
import Allshopping from "./Shop/Allshopping";
import { CartProvider } from "react-use-cart";
import Allcart from "./Cart/Cart";

const router = createBrowserRouter([
  {
    path: "/",
    element:<App/>,
  },
  {
    path: "properties",
    element: <Properties/>,
  },
  {
    path: "details",
    element: <Alldetail/>,
  },
  {
    path: "contact us",
    element: <Contact/>,
  },
  {
    path: "Products",
    element: <Allproducts/>,
  },
  {
    path: "shop",
    element: <Allshopping/>,
  },
  {
    path: "cart",
    element: <Allcart/>,
  },


]);

createRoot(document.getElementById("root")).render(
  <RouterProvider router={router} />
);

